<template>
  <div v-show="typeSelectOptions.length"
    :class="['explicitrule-box', showRight ? '' : 'switch-type-box-content']">
    <formItem>
      <el-select v-model="currentType" :class="['switch-type-box', 'switch-type-box-left']"
        :popper-append-to-body="false" size="mini" @change="selectTypeChange" placeholder="请选择">
        <el-option v-for="item in typeSelectOptions" :key="item.value" class="telephone-option" :label="item.label"
          :value="item.value">
        </el-option>
      </el-select>
      <el-select size="mini" @change="selectValueChange" v-show="showRight" class="switch-type-box"
        :popper-append-to-body="false" slot="field" v-model="selectValue"
        :placeholder="optionDisabled ? '座席绑定' : '请选择'">
        <el-option v-for="item in selectOptions" :key="item.value" :label="item.label" :value="item.value">
          <!-- <span v-if="optionDisabled"><span style="margin-right: 15px">{{item.value}}</span>{{item.city}}</span> -->
          <span>{{ item.label }}</span>
        </el-option>
      </el-select>
    </formItem>
  </div>
</template>

<script>
import formItem from './formItem'
export default {
  props: {
    currentLoginType: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      currentType: '',
      selectOptions: [],
      selectValue: '',
      typeSelect: []
    }
  },
  components: {
    formItem
  },
  computed: {
    showRight() {
      return this.currentType !== 'accountNumber' && this.currentType !== 'line' && this.currentType !== 'normal' && this.currentType !== 'shuanghu'
    },
    optionDisabled() {
      if (this.currentType === 'small') {
        if (this.currentSmallType === 'A') {
          return true
        } else {
          return false
        }
      } else {
        return false
      }
    },
    typeSelectOptions() {
      let arr = []
      if (this.currentLoginType === "Local") {
        arr = this.typeSelect.map(o => {
          return {
            ...o
          }
        })
      } else {
        arr = this.typeSelect.filter(i => i.value !== 'shuanghu')
      }
      if (arr.length === 1) {
        this.currentType = arr[0].value
      }
      this.$nextTick(() => {
        this.selectTypeChange(this.currentType)
      })
      return arr
    }
  },
  methods: {
    selectTypeChange(value) {
      window.webapp.agentApi.setCurrentExplicitRule({
        type: value
      })
      this.$emit('changeExplicitRule', value)
      this.typeChange()
    },
    selectValueChange(val) {
      window.webapp.agentApi.setCurrentExplicitVale({
        value: val,
        success: (res) => {
        }
      })
    },
    typeChange(selectValue) {
      let that = this
      console.log('hhhhhhhhhhhhhhhhhhhhhhhhhhhhhhhh', that.currentType)
      window.webapp.agentApi.getExplicitDataByType({
        type: that.currentType,
        success: (res) => {
          console.log(res, 'hhhhhhhhhhhhhhhhhhhhhhhhhh')
          this.selectOptions = res.data;
          if (res.data.length) {
            this.selectValue = res.data[0].value;
            this.selectValueChange(this.selectValue);
          }
        }
      })
    },
    getTypeSelect() {
      let that = this;
      if (window.token && window.webapp) {
        window.webapp.agentApi.getExplicitRules({
          success: (data) => {
            // that.typeSelect = data.data.filter(i => ['normal', 'shuanghu'].includes(i.value)) || []
            that.typeSelect = data.data || [];
            if (that.typeSelect.length === 1) {
              that.selectTypeChange(that.typeSelect[0].value)
            }
          }
        })
      } else {
        setTimeout(() => {
          this.getTypeSelect()
        }, 100)
      }
    },

    /**
     * @description: 获取当前的外显规则
     * @return {*}
     */
    getCurrentType() {
      if (window.token && window.webapp) {
        this.currentType = window.webapp.agentApi.getCurrentExplicitRule();
        this.$emit('changeExplicitRule', this.currentType)
      } else {
        setTimeout(() => {
          this.getCurrentType()
        }, 100)
      }
    }
  },
  mounted() {
    this.getTypeSelect()
    this.getCurrentType()
  }
}
</script>
